<template>
  <div class="type-nav">
    <div class="container">
      <!-- 事件的委派，将子元素行为样式委托给父元素 -->
      <div @mouseleave="leaveShow" @mouseenter="enterShow">
        <h2 class="all">全部商品分类</h2>
        <div class="sort" @click="goSearch" v-show="show">
          <div class="all-sort-list2">
            <!-- 一级菜单，索引值 -->
            <div class="item hyperlink" v-for="(c1, index) in categoryList" :key="c1.categoryId"
                 :class="{cur: currentIndex === index}">
              <h3 @mouseenter="changeIndex(index)">
                <a :data-categoryName="c1.categoryName" :data-category1Id="c1.categoryId">{{ c1.categoryName }}</a>
              </h3>
              <div class="item-list clearfix" :style="{display: currentIndex === index ? 'block' : 'none'}">
                <div class="subitem" v-for="(c2, index) in c1.categoryChild" :key="c2.categoryId">
                  <dl class="fore">
                    <dt>
                      <a :data-categoryName="c2.categoryName" :data-category2Id="c2.categoryId">{{
                          c2.categoryName
                        }}</a>
                    </dt>
                    <dd>
                      <em v-for="(c3, index) in c2.categoryChild" :key="c3.categoryId" class="hyperlink">
                        <a :data-categoryName="c3.categoryName" :data-category3Id="c3.categoryId">{{
                            c3.categoryName
                          }}</a>
                      </em>
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <nav class="nav">
        <a href="###">服装城</a>
        <a href="###">美妆馆</a>
        <a href="###">超市</a>
        <a href="###">全球购</a>
        <a href="###">闪购</a>
        <a href="###">团购</a>
        <a href="###">有趣</a>
        <a href="###">秒杀</a>
      </nav>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
// 按需引入，性能优化
import throttle from 'lodash/throttle'

export default {
  name: "TypeNav",
  data() {
    return {
      currentIndex: -1,
      show: true
    }
  },
  mounted() {
    // 通过判断实现除主页外的路由组件默认隐藏TypeNav
    if (this.$route.path != '/home') {
      this.show = false
    }
  },
  computed: {
    ...mapState({
      // 对象写法右侧为一个函数，当使用该计算属性时，右侧函数会立即执行一次，注入state参数，即为大仓库的state
      categoryList: state => state.Home.categoryList
    })
  },
  methods: {
    // throttle函数最好使用普通函数而不是箭头函数，防止上下文出现问题
    changeIndex: throttle(function (index) {
      this.currentIndex = index
    }, 50),
    goSearch(event) {
      let element = event.target
      // 节点具有dataset属性，可以获取节点的自定义属性与属性值
      // 浏览器自动将自定义属性名自动全部小写而非保持驼峰
      let {categoryname, category1id, category2id, category3id} = element.dataset
      if (categoryname) {
        // 整理路由跳转的参数
        let location = {name: 'search'}
        let query = {categoryName: categoryname}
        if (category1id) {
          query.category1Id = category1id
        } else if (category2id) {
          query.category2Id = category2id
        } else if (category3id) {
          query.category3Id = category3id
        }
        // query与params参数合并
        if (this.$route.params) {
          location.params = this.$route.params
          // 拼接参数
          location.query = query
          // 路由跳转
          this.$router.push(location)
        }
      }
    },
    enterShow() {
      this.show = true
    },
    leaveShow() {
      this.currentIndex = -1
      if (this.$route.path != '/home') {
        this.show = false
      }
    }
  }
}
</script>

